import React, {
    SelectHTMLAttributes,
    DetailedHTMLProps,
    ChangeEvent
} from 'react';

import styles from './SuperSelect.module.css';

type DefaultSelectPropsType = DetailedHTMLProps<SelectHTMLAttributes<HTMLSelectElement>,
    HTMLSelectElement>;

type SuperSelectPropsType = DefaultSelectPropsType & {
    options?: any[];
    onChangeOption?: (option: any) => void;
};

const SuperSelect: React.FC<SuperSelectPropsType> = props => {
    const {
        options,
        onChange,
        className,
        onChangeOption,
        ...restProps
    } = props;

    const mappedOptions: Array<any> | null = options
        ? options.map((el, index) => <option key={index} className={styles.option}>{el}</option>)
        : null; // map options with key

    const onChangeCallback = (e: ChangeEvent<HTMLSelectElement>) => {
        onChangeOption && onChangeOption(e.currentTarget.value);
        onChange && onChange(e);
    };

    return (
        <select onChange={onChangeCallback} className={styles.select} {...restProps}>
            {mappedOptions}
        </select>
    );
};

export default SuperSelect;
