import React, {useState} from 'react';
import AlternativeSuperButton from '../h4/common/c2-SuperButton/AlternativeSuperButton';

const API = 'https://neko-cafe-back.herokuapp.com/auth/test';

export const HW13: React.FC = () => {
    const [response, setResponse] = useState<any>(null);
    const [getError, setGetError] = useState<boolean>(false);
    const [error, setError] = useState<any>(null);
    const [loading, setLoading] = useState<boolean>(false);

    const onClickHandler = () => {
        setLoading(true);
        setResponse(null);
        setError(null);
        fetch(API, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json;charset=utf-8'
            },
            body: JSON.stringify({success: !getError})
        })
            .then((res) => res.json())
            .then(setResponse, setError)
            .finally(() => setLoading(false));
    };

    return (
        <>
            <hr/>
            homeworks 13

            <div>{loading ? (
                <span>Loading...</span>
            ) : (
                <>
                    <div style={{display: 'flex', alignItems: 'center', justifyContent: 'flex-start', margin: '10px'}}>
                        <label htmlFor={'error'}>Получить ошибку?</label>
                        <input
                            style={{fontSize: '16px', width: '20px', height: '20px', marginLeft: '10px'}}
                            type="checkbox"
                            name="error"
                            checked={getError}
                            onChange={(e) => setGetError(e.currentTarget.checked)}/>

                    </div>
                    <div>
                        <AlternativeSuperButton
                            color={'green'}
                            w={150}
                            h={40}
                            onClick={onClickHandler}
                        >
                            Отправить
                        </AlternativeSuperButton>
                    </div>
                </>
            )}
                {response && <pre>{JSON.stringify(response, null, 2)}</pre>}
                {error && <pre>{JSON.stringify(error, null, 2)}</pre>}</div>
        </>
    );
};
