const SET_THEME = 'SET_THEME';

export enum Theme { dark = 'dark', light = 'light', blue = 'blue', red = 'red'}

type StateType = {
    theme: Theme
}

const initState: StateType = {
    theme: Theme.red
};

type ThemeReducerActionType = ChangeThemeActionType

export const themeReducer = (state = initState, action: ThemeReducerActionType): StateType => { // fix any
    switch (action.type) {
        case 'SET_THEME': {
            return {
                ...state, theme: action.payload.theme
            };
        }
        default:
            return state;
    }
};

type ChangeThemeActionType = ReturnType<typeof ChangeTheme>

export const ChangeTheme = (theme: Theme) => ( {
    type: SET_THEME,
    payload: {theme}
} as const );