import React from 'react';
import s from './HW12.module.css';
import {useDispatch, useSelector} from 'react-redux';
import {AppStoreType} from '../h10/bll/store';
import {ChangeTheme, Theme} from './bll/themeReducer';
import SuperSelect from '../h7/common/c5-SuperSelect/SuperSelect';

const HW12: React.FC = () => {
    const theme = useSelector<AppStoreType, Theme>(state => state.theme.theme);
    const dispatch = useDispatch();
    let themes = Object.values(Theme);
    const onChangeCallback = (option: Theme) => {
        dispatch(ChangeTheme(option));
    };

    return (
        <div className={s[theme]}>
            <hr/>
            <span className={s[theme + '-text']}>
                homeworks 12
            </span>
            <div>
                <SuperSelect
                    options={themes}
                    value={theme}
                    onChangeOption={onChangeCallback}/>
            </div>
        </div>
    );
};

export default HW12;
